.App {
  text-align: center;
}
body {
  background: #23282c;
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
input {
  border: 0;
  padding: 10px;
  font-size: 18px;
  font-family: "Helvetica Neue";
  font-weight: 100;
}

input[type="submit"] {
  background: #B96764;
  color: white;
  font-family: "Helvetica Neue";
  cursor: pointer;
  font-weight: 100;
}